import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'Index',
      component: ()=>import('../components/index')
    },
    {
      path: '/404',
      name: '404',
      component: ()=>import('../components/404')
    },
    {
      path:'/login',
      name:'login',
      component: ()=>import('../components/login')
    },
    {
      path: '/reset-pwd',
      name:'resetPassword',
      component: ()=>import('../components/reset-pwd')
    },
    {
      path:'/gacha/pcr/pool-setting',
      name:'pcr-pool-setting',
      meta:{auth:true},
      component: ()=>import('../components/gacha/pcr-pool-setting')
    },
    {
      path:'/plugins-dict',
      name:'plugins-dict',
      component: ()=>import('../components/plugins-dict')
    },
    {
      path: '*',
      redirect: '/404'
    }
  ]
})

export function addRouterInterceptor() {
  router.beforeEach((to,from,next) => {
    if(to.matched.some( m => m.meta&&m.meta.auth )) {
      checkAuth().then(()=>next()).catch(err=>next(err))
    } else {
      next()
    }
  })
}

export function checkAuth(vue) {
  return new Promise((resolve, reject) => {
    let token = vue.$cookies.get('token')
    let salt = vue.$cookies.get('salt')
    if(token&&salt){
      vue.$ajax({
        url:'/token.valid',
        params:{token:token,salt:salt}
      }).then(res=>{
        if (res['code']===0){
          resolve()
        }else {
          vue.$message({
            type:'error',
            message:res['msg'],
            duration:1000,
            onClose:()=>{
              reject('/login')
            }
          })
        }
      }).catch(e=>{
        reject('/index')
      })
    }else{
      vue.$message({
        type:'error',
        message:'未登录',
        duration:1000,
        onClose:()=>{
          reject('/login')
        }
      })
    }
  })
}

export function checkIsAdmin(vue,userId) {
  return new Promise((resolve, reject) => {
    let user_id = userId?userId:vue.$cookies.get('user_id')
    if (user_id) {
      vue.$ajax({url: '/checkIsAdmin.valid', params: {user_id: user_id}})
        .then(res => {
          if (res['code'] === 0) {
            resolve()
          }else reject()
        })
        .catch(err=>{
          reject()
        })
    }else
      reject()
  })
}



