<template>
  <div id="app">
    <router-view/>
    <url-conf v-if="isAdmin"></url-conf>
  </div>
</template>

<script>
  import urlConf from './components/url-prefix-conf'

  export default {
    name: 'App',
    components: {
      'url-conf': urlConf
    },
    data(){
      return{
        isAdmin:false
      }
    },
    created() {
      let user_id = this.$cookies.get('user_id')
      this.checkAdmin(user_id).then(()=>this.isAdmin=true).catch(()=>this.isAdmin=false)
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /*text-align: center;*/
    color: #2c3e50;
    /*margin-top: 60px;*/
  }

  label.el-form-item__label:after {
    content: ': ';
  }

  .text-center {
    text-align: center !important;
  }

  .text-left {
    text-align: left !important;
  }

  .text-right {
    text-align: right !important;
  }

  .display-inline-block {
    display: inline-block;
  }

  .el-tooltip__popper.is-light {
    border: 1px solid #b5b5b5 !important;
  }
</style>



